@-webkit-keyframes buttonScrollDownAnimation {
    from {-webkit-transform: translateY(0);transform: translateY(0);}
    to {-webkit-transform: translateY(6px);transform: translateY(6px);}
}

@keyframes buttonScrollDownAnimation {
    from {-webkit-transform: translateY(0);transform: translateY(0);}
    to {-webkit-transform: translateY(6px);transform: translateY(6px);}
}

@-webkit-keyframes buttonScrollTopAnimation {
    from {-webkit-transform: translateY(6px);transform: translateY(6px);}
    to {-webkit-transform: translateY(0);transform: translateY(0);}
}

@keyframes buttonScrollTopAnimation {
    from {-webkit-transform: translateY(6px);transform: translateY(6px);}
    to {-webkit-transform: translateY(0);transform: translateY(0);}
}

button.scrollDown {
    position: absolute;
    bottom: 0;
    right: calc(50% - 27px);
    background: none!important;
    border: none!important;
    box-shadow: none!important;
    -webkit-animation-name: buttonScrollDownAnimation;
            animation-name: buttonScrollDownAnimation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

button.scrollTop {
    position: absolute;
    bottom: 15px;
    right: calc(50% - 27px);
    background: none!important;
    border: none!important;
    box-shadow: none!important;
    -webkit-animation-name: buttonScrollTopAnimation;
            animation-name: buttonScrollTopAnimation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

button.scrollDown svg , button.scrollTop svg {
    width: 30px!important;
    height: 30px!important;
}

#selectLanguage {
    position: absolute;
    top: 15px;
    left: 10px;
    z-index: 99;
    border: 1px solid rgba(255, 255, 255, 0.3);
}

#selectLanguage div {
    color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

#selectLanguage div:hover {
    background-color: rgba(255, 255, 255, 0.2);;
}

#selectLanguage #persianLanguage {
    border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.cursorPointer {
    cursor: pointer;
}

.accessDenied {
    height: calc(100vh - 82px);
    display: flex;
    align-items: center;
    justify-content: center;
