@import url(//db.onlinewebfonts.com/c/0b0a8c345731751b990403a2cf40fbec?family=IRAN+Sans);
@font-face {font-family: "IRAN Sans"; src: url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.eot"); src: url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.woff") format("woff"), url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/0b0a8c345731751b990403a2cf40fbec.svg#IRAN Sans") format("svg"); }

html, body{
    font-family: "IRAN Sans"!important;
    overflow-x: hidden;
    min-height: 100vh;
}

.allTransitions{
    transition: all 0.3s;
}

.font-14 {
    font-size: 14px;
}

.font-16 {
    font-size: 16px;
}

.font-18 {
    font-size: 18px;
}

.font-20 {
    font-size: 20px;
}

.whiteBackground {
    background-color: #fff;
}

.blackBackground {
    background-color: #000;
}

.blueBackground {
    background-color: #07386a;
}

.redBackground {
    background-color: #6c1019;
}

.whiteColor {
    color: #fff;
}

a:hover {
    text-decoration: none!important;
}

.borderBottom-1px {
    border-bottom: 1px solid silver;
}

@keyframes buttonScrollDownAnimation {
    from {transform: translateY(0);}
    to {transform: translateY(6px);}
}

@keyframes buttonScrollTopAnimation {
    from {transform: translateY(6px);}
    to {transform: translateY(0);}
}

button.scrollDown {
    position: absolute;
    bottom: 0;
    right: calc(50% - 27px);
    background: none!important;
    border: none!important;
    box-shadow: none!important;
    animation-name: buttonScrollDownAnimation;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-play-state: running;
}

button.scrollTop {
    position: absolute;
    bottom: 15px;
    right: calc(50% - 27px);
    background: none!important;
    border: none!important;
    box-shadow: none!important;
    animation-name: buttonScrollTopAnimation;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-play-state: running;
}

button.scrollDown svg , button.scrollTop svg {
    width: 30px!important;
    height: 30px!important;
}

#selectLanguage {
    position: absolute;
    top: 15px;
    left: 10px;
    z-index: 99;
    border: 1px solid rgba(255, 255, 255, 0.3);
}

#selectLanguage div {
    color: rgba(255, 255, 255, 0.8);
    cursor: pointer;
}

#selectLanguage div:hover {
    background-color: rgba(255, 255, 255, 0.2);;
}

#selectLanguage #persianLanguage {
    border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.cursorPointer {
    cursor: pointer;
}

.accessDenied {
    height: calc(100vh - 82px);
    display: flex;
    align-items: center;
    justify-content: center;
    color: red;
    font-size: 40px;
}