.homepageTopContent {
    background-image: url('/Images/Backgrounds/HomePage/Background.jpg');
    background-size: 100% 100%;
    height: 100vh;
    width: 100%;
}
  
@media (max-width: 1024px) and (min-width: 769px) {
    .homepageTopContent {
      height: 50vh;
    }
}

.homepageTopContent .navbar {
  flex-direction: row-reverse;
}

.homepageTopContent .navbar .navbar-toggler {
  width: 79px!important;
  border-color: rgba(255, 255, 255, 0.3)!important;
  color: rgba(255, 255, 255, 0.8)!important;
  border-radius: 0!important;
  box-shadow: none!important;
  outline: none!important;
} 

.homepageTopContent .navbar .navbar-collapse ul {
  float: right;
  border:1px solid rgba(255, 255, 255, 0.3);
  border-top: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
} 

.homepageTopContent .navbar .navbar-collapse {
  width: 77px!important;
}

.homepageTopContent .navbar .navbar-collapse ul li {
  transition: all 0.3s;
  width: 77px!important;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0 5px;
  display: flex;
}

.homepageTopContent .navbar .navbar-collapse ul li:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.homepageTopContent .navbar .navbar-collapse ul li:last-child {
  border: none!important;
}

.homepageTopContent .navbar .navbar-collapse ul li a {
  width: 77px!important;
  color: rgba(255, 255, 255, 0.8);
} 

.infoDiv {
  position: absolute;
  top: 70%;
  font-size: 28px;
}

@media (max-width: 370px) {
  .infoDiv {
    font-size: 16px;
  }
}